export const InputDropdown: FC<{ searchBooks: IBook[]}> = ({ searchBooks }) => {

    // const closeInp = () => () => showDropdown.current?.classList.remove(styles.opened)

    return (
        <AnimatePresence>
            <motion.ul className={styles.bookList}>
                {searchBooks.map(book => (
                    <li key={book.id} className={styles.bookItem}>

                        <Link to={'book/' + book.id} className={styles.bookItem__content}>
                            <div>
                                <p className={styles.bookItem__title}>{book.title}</p>
                                <p className={styles.bookItem__author}>{book.author}</p>
                            </div>
                            <img src={book.image} alt={book.title} width={40} />
                        </Link>

                    </li>
                ))}
            </motion.ul>
        </AnimatePresence>
    )
}